<template>
    <div id="dashboard">
        <div class="section">
            <h3 class="title">Dashboard</h3>
            <router-link to='/new' class="button is-primary">
                <span class="icon"><i class="fa fa-plus"></i></span> 
                <span>New Employee</span>           
            </router-link>
        </div>
        <ul class="panel">
            <li class="panel-heading"><h4 class="title is-5">Employee</h4></li>
  
            <li class="panel-block" v-for="employee in employees" v-bind:key="employee.id">
                
                <router-link v-bind:to="{name:'view-employee', params:{id:employee.id}}">
                {{employee.employee_id}} {{employee.name}} 
                </router-link>
                <span class="tag is-info is-rounded">{{employee.dept}}</span>
            </li>

  
        </ul>
    </div>
    
</template>


<script>
import db from "./firebaseInit";
export default {
  name: "dashboard",
  data() {
    return {
      employees: []
    };
  },

  created() {
    db
      .collection("employees")
      .orderBy("employee_id")
      .get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc => {
          const data = {
            id: doc.id,
            employee_id: doc.data().employee_id,
            name: doc.data().name,
            dept: doc.data().dept,
            position: doc.data().position
          };
          this.employees.push(data);
        });
      });
  }
};
</script>




