<template>
    <div id="edit-employee">
       <h3 class="title is-size 3">Edit Employee</h3>
        <form @submit.prevent="updateEmployee">
            <div class="field ">
              <label class="label">Employee ID#</label>
              <div class="control">
                <input class="input" type="text" v-model="employee_id" disabled>
              </div>
            </div>
            <div class="field">
              <label class="label">Name</label>
              <div class="control">
                <input class="input" type="text" v-model="name" required>
              </div>
            </div>
            <div class="field">
              <label class="label">Department</label>
              <div class="control">
                <input class="input" type="text" v-model="dept" required>
              </div>
            </div>
            <div class="field">
              <label class="label">Position</label>
              <div class="control">
                <input class="input" type="text" v-model="position" required>
              </div>
            </div>
            <div class="field is-grouped">
                <div class="control">
                  <button type="submit" class="button is-primary">Save Changes</button>
                </div>
                <div class="control">
                  <router-link to="/" class="button is-link">Cancel</router-link>
                </div>
            </div>

        </form>   
    </div>
</template>


<script>
import db from "./firebaseInit";
var employeesRef = db.collection("employees");
export default {
  name: "edit-employee",
  data() {
    return {
      employee_id: null,
      name: null,
      dept: null,
      position: null
    };
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.fetchData(to.params.id);
    });
  },
  watch: {
    $route: "fetchData"
  },
  methods: {
    fetchData(id) {
      if (!id) id = this.$route.params.id;
      employeesRef
        .doc(id)
        .get()
        .then(doc => {
          if (doc.exists) {
            this.employee_id = doc.data().employee_id;
            this.name = doc.data().name;
            this.dept = doc.data().dept;
            this.position = doc.data().position;
          }
        });
    },
    updateEmployee(){
      employeesRef
        .doc(this.$route.params.id)
        .get()
        .then(doc => {
          doc.ref.update({
        employee_id: this.employee_id,
        name:this.name,
        dept:this.dept,
        position:this.position
      })
      .then(() => this.$router.push({name:'view-employee', params:{id:this.$route.params.id}}))        
      .catch(err => console.log(err))
        });
      
    }
  }
};
</script>




