<template>
    <div id="new-employee">
        <h3 class="title is-size 3">New Employee</h3>
        <form @submit.prevent="saveEmployee">
            <div class="field">
              <label class="label">Employee ID#</label>
              <div class="control">
                <input class="input" type="text" v-model="employee_id" required>
              </div>
            </div>
            <div class="field">
              <label class="label">Name</label>
              <div class="control">
                <input class="input" type="text" v-model="name" required>
              </div>
            </div>
            <div class="field">
              <label class="label">Department</label>
              <div class="control">
                <input class="input" type="text" v-model="dept" required>
              </div>
            </div>
            <div class="field">
              <label class="label">Position</label>
              <div class="control">
                <input class="input" type="text" v-model="position" required>
              </div>
            </div>
            <div class="field is-grouped">
                <div class="control">
                  <button type="submit" class="button is-primary">Submit</button>
                </div>
                <div class="control">
                  <router-link to="/" class="button is-link">Cancel</router-link>
                </div>
            </div>

        </form>   
 
    </div>
</template>


<script>
import db from './firebaseInit'
export default {
  name: "new-employee",
  data() {
    return {
      employee_id: null,
      name:null,
      dept:null,
      position:null
    }
  },
  methods: {
    saveEmployee(){
      db.collection('employees').add({
        employee_id: this.employee_id,
        name:this.name,
        dept:this.dept,
        position:this.position
      })
      .then(docRef => this.$router.push('/'))        
      .catch(err => console.log(err))
    }
  }
};
</script>




