<template>
    <div id="view-employee">
       <ul class="panel">
            <li class="panel-heading"><h4 class="title is-4">{{name}}</h4></li>
            <li class="panel-block">Employee ID: {{employee_id}}</li>
            <li class="panel-block">Department: {{dept}}</li>
            <li class="panel-block">Position: {{position}}</li>
        </ul>
        <div class="field is-grouped">
            <p class="control">
              <router-link to="/"  class="button is-link">
                Back
              </router-link>
            </p>
            <p class="control">
              <router-link v-bind:to="{name:'edit-employee', param: {employee_id:employee_id}}" class="button is-primary">
                Edit
              </router-link>
            </p>
            <p class="control">
              <button @click ='deleteEmployee' class="button is-danger">
                Delete
              </button>
            </p>
          </div>
    </div>
</template>


<script>
import db from "./firebaseInit";
var employeesRef = db.collection("employees");
export default {
  name: "view-employee",
  data() {
    return {
      employee_id: null,
      name: null,
      dept: null,
      position: null
    };
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.fetchData(to.params.id);
    });
  },
  watch: {
    $route: "fetchData"
  },
  methods: {
    fetchData(id) {
      if (!id) id = this.$route.parms.id;
      employeesRef
        .doc(id)
        .get()
        .then(doc => {
          if (doc.exists) {
            this.employee_id = doc.data().employee_id;
            this.name = doc.data().name;
            this.dept = doc.data().dept;
            this.position = doc.data().position;
          }
        });
    },
    deleteEmployee() {
      if (confirm("Are you sure?")) {
        employeesRef
          .doc(this.$route.params.id)
          .get()
          .then(doc => {
            doc.ref.delete();
            this.$router.push("/");
          });
      }
    }
  }
};
</script>




